body{
  background-color: black;
}

.App{
  background-color: tomato;
  min-height: 550px;
  width: 420px;
  margin: 100px 0 0 500px;
}

#to-do-form input{
  background-color: white;
  width : 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  border-radius: 5px;
  color: black;
  font-weight: bolder;
}

#to-do-form ::placeholder{
  color: black;
  font-weight: bold;  
}

#to-do-form button{
  height: 50px;
  width : 100px;
  border-radius: 5px;
  border: 0px;
  font-weight: bolder;
  background-color:  white;
}

.listItem{
  color: white;
  width:  300px;
  background-color : black;
  height: 50px;
  margin : 20px auto;
  border-radius: 5px;
}

.listItem p{
  padding: 10px; 
  position: relative;
}

.listItem p span{
  position: absolute;
  right: 10px;
  cursor: pointer;
}

